import { createReducer } from '@reduxjs/toolkit';
import { CategoryState } from '../../types/Category';
import { requestCategories, requestCategoriesError, requestCategoriesSuccess } from './actions';

const categoryReducer = createReducer<CategoryState>({
  category: null,
  loading: false,
  error: null,
}, {
  [requestCategories.type]: (state) => ({
    ...state,
    category: null,
    loading: true,
    error: null,
  }),
  [requestCategoriesSuccess.type]: (state, { payload }) => ({
    ...state,
    category: payload,
    loading: false,
    error: null,
  }),
  [requestCategoriesError.type]: (state, { payload }) => ({
    ...state,
    category: null,
    loading: false,
    error: payload,
  }),
});

export default categoryReducer;
